workbox_routing_registerRoute(/\.(?:js|css|woff2?|png|jpg|jpeg|svg|gif|webp|avif)$/i, new workbox_strategies_StaleWhileRevalidate({ "cacheName":"static-assets", plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 200, maxAgeSeconds: 2592000 }), { handlerDidError: async ({ request }) => self.fallback(request) }] }), 'GET');
workbox_routing_registerRoute(/^https?:\/\/.*$/i, new workbox_strategies_NetworkFirst({ "cacheName":"pages","networkTimeoutSeconds":3, plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 50, maxAgeSeconds: 86400 }), { handlerDidError: async ({ request }) => self.fallback(request) }] }), 'GET');




